<template><section><h1>CzmlDataSource</h1>
<p><code v-pre>vc-datasource-czml</code> 组件用于加载 <a href="https://github.com/AnalyticalGraphicsInc/czml-writer/wiki/CZML-Guide">CZML</a> 格式数据源。</p>
<h2>示例</h2>
<h3>加载 CZML 数据源</h3>
<h4>预览</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer :shouldAnimate="true" @ready="ready">
        <vc-datasource-czml czml="./statics/SampleData/simple.czml" @ready="subReady" :show="show" @click="clicked"></vc-datasource-czml>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
<h4>代码</h4>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"viewer"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">vc-viewer</span> <span class="hljs-attr">:shouldAnimate</span>=<span class="hljs-string">"true"</span> <span class="hljs-attr">@ready</span>=<span class="hljs-string">"ready"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">vc-datasource-czml</span>
        <span class="hljs-attr">czml</span>=<span class="hljs-string">"./statics/SampleData/simple.czml"</span>
        <span class="hljs-attr">@ready</span>=<span class="hljs-string">"subReady"</span>
        <span class="hljs-attr">:show</span>=<span class="hljs-string">"show"</span>
        <span class="hljs-attr">@click</span>=<span class="hljs-string">"clicked"</span>
      &gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vc-datasource-czml</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">vc-viewer</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">show</span>: <span class="hljs-literal">true</span>
      }
    },
    <span class="hljs-attr">methods</span>: {
      ready(cesiumInstance) {},
      subReady(cesiumInstance) {
        <span class="hljs-keyword">const</span> { Cesium, viewer } = cesiumInstance
        viewer.camera.flyHome(<span class="hljs-number">0</span>)
      },
      clicked(e) {
        <span class="hljs-built_in">console</span>.log(e)
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
<h2>属性</h2>
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>czml</td>
<td>String|Object</td>
<td></td>
<td><code v-pre>required</code> 指定 czml 对象或者 url。</td>
</tr>
<tr>
<td>show</td>
<td>Boolean</td>
<td><code v-pre>true</code></td>
<td><code v-pre>optional</code> 指定数据源是否可见。</td>
</tr>
<tr>
<td>entities</td>
<td>Array</td>
<td><code v-pre>[]</code></td>
<td><code v-pre>optional</code>指定要添加到该数据源的实体集合。</td>
</tr>
<tr>
<td>options</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> 指定以下属性：</td>
</tr>
<tr>
<td>---------</td>
<td>--------------</td>
<td>------</td>
<td>------------------------------------------</td>
</tr>
<tr>
<td>sourceUri</td>
<td>String</td>
<td></td>
<td></td>
</tr>
<tr>
<td>credit</td>
<td>String|Object</td>
<td></td>
<td></td>
</tr>
</tbody>
</table>
<hr>
<ul>
<li>参考官方文档： <strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/CzmlDataSource.html">CzmlDataSource</a></strong></li>
</ul>
<h2>事件</h2>
<table>
<thead>
<tr>
<th>事件名</th>
<th>参数</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>该组件渲染完毕时触发，返回 Cesium 类, viewer 实例，以及当前组件的 cesiumObject。</td>
</tr>
<tr>
<td>changedEvent</td>
<td></td>
<td>数据源改变时触发。</td>
</tr>
<tr>
<td>errorEvent</td>
<td></td>
<td>数据源发生错误时触发。</td>
</tr>
<tr>
<td>loadingEvent</td>
<td></td>
<td>数据源开始或结束加载时触发。</td>
</tr>
<tr>
<td>clusterEvent</td>
<td>(clusteredEntities, cluster)</td>
<td>数据源聚合事件。</td>
</tr>
<tr>
<td>collectionChanged</td>
<td>(collection, added, removed, changed)</td>
<td>数据源实体集合改变时触发。</td>
</tr>
<tr>
<td>mousedown</td>
<td>{button,surfacePosition,target,type,windowPosition}</td>
<td>鼠标在该数据源上按下时触发。</td>
</tr>
<tr>
<td>mouseup</td>
<td>{button,surfacePosition,target,type,windowPosition}</td>
<td>鼠标在该数据源上弹起时触发。</td>
</tr>
<tr>
<td>click</td>
<td>{button,surfacePosition,target,type,windowPosition}</td>
<td>鼠标单击该数据源时触发。</td>
</tr>
<tr>
<td>dblclick</td>
<td>{button,surfacePosition,target,type,windowPosition}</td>
<td>鼠标左键双击该数据源时触发。</td>
</tr>
<tr>
<td>mousemove</td>
<td>{button,surfacePosition,target,type,windowPosition}</td>
<td>鼠标移动到该数据源时触发。</td>
</tr>
</tbody>
</table>
<hr>
</section></template>

<script>
    export default {
      data() {
        return {
          show: true
        }
      },
      methods: {
        ready(cesiumInstance) {},
        subReady(cesiumInstance) {
          const { Cesium, viewer } = cesiumInstance
          viewer.camera.flyHome(0)
        },
        clicked(e) {
          console.log(e)
        }
      }
    }
  </script>